const _status: Record<string | symbol, number> = {};

/**
 * @en Increment reference count for status identifier.
 * @ja 
 *
 * @param status
 *  - `en` state identifier
 *  - `ja` 
 * @returns
 *  - `en` reference count value
 *  - `ja` 
 */
export function statusAddRef(status: string | symbol): number {
    if (!_status[status]) {
        _status[status] = 1;
    } else {
        _status[status]++;
    }
    return _status[status];
}

/**
 * @en Decrement reference count for status identifier.
 * @ja 
 *
 * @param status
 *  - `en` state identifier
 *  - `ja` 
 * @returns
 *  - `en` reference count value
 *  - `ja` 
 */
export function statusRelease(status: string | symbol): number {
    if (!_status[status]) {
        return 0;
    } else {
        const retval = --_status[status];
        if (0 === retval) {
            delete _status[status];
        }
        return retval;
    }
}

/**
 * @en State variable management scope
 * @ja 
 *
 * @param status
 *  - `en` state identifier
 *  - `ja` 
 * @param executor
 *  - `en` seed function.
 *  - `ja` 
 * @returns
 *  - `en` retval of seed function.
 *  - `ja` 
 */
export async function statusScope<T>(status: string | symbol, executor: () => T | Promise<T>): Promise<T> {
    try {
        statusAddRef(status);
        return await executor();
    } finally {
        statusRelease(status);
    }
}

/**
 * @en Check if it's in the specified state.
 * @ja 
 *
 * @param status
 *  - `en` state identifier
 *  - `ja` 
 * @return {Boolean} true:  / false: 
 * @returns
 *  - `en` `true`: within the status / `false`: out of the status
 *  - `ja` `true`:  / `false`: 
 */
export function isStatusIn(status: string | symbol): boolean {
    return !!_status[status];
}
