import {
    isFunction,
    isString,
    noop,
    setMixClassAttribute,
} from '@cdp/core-utils';
import { document } from './ssr';
import { isWindowContext } from './utils';
import {
    ElementBase,
    SelectorBase,
    QueryContext,
    DOM,
    DOMSelector,
    DOMResult,
    DOMIterateCallback,
    dom as $,
} from './static';
import {
    DOMBase,
    DOMIterable,
    isNode,
    isNodeElement,
    isNodeQueriable,
    isTypeElement,
    isTypeWindow,
    isEmptySelector,
    isStringSelector,
    isDocumentSelector,
    isWindowSelector,
    isNodeSelector,
    isIterableSelector,
    nodeName,
    getOffsetParent,
} from './base';

export type DOMModificationCallback<T extends ElementBase, U extends ElementBase> = (index: number, element: T) => U;

/** @internal helper for `is()` and `filter()` */
function winnow<T extends SelectorBase, U extends ElementBase>(
    selector: DOMSelector<T> | DOMIterateCallback<U>,
    dom: DOMTraversing<U>,
    validCallback: (el: U) => unknown,
    invalidCallback?: () => unknown,
): any {
    invalidCallback = invalidCallback ?? noop;

    let retval: unknown;
    for (const [index, el] of dom.entries()) {
        if (isFunction(selector)) {
            if (selector.call(el, index, el)) {
                retval = validCallback(el);
                if (undefined !== retval) {
                    return retval;
                }
            }
        } else if (isStringSelector(selector)) {
            if ((el as Node as Element).matches?.(selector)) {
                retval = validCallback(el);
                if (undefined !== retval) {
                    return retval;
                }
            }
        } else if (isWindowSelector(selector)) {
            if (isWindowContext(el)) {
                retval = validCallback(el);
                if (undefined !== retval) {
                    return retval;
                }
            } else {
                retval = invalidCallback();
                if (undefined !== retval) {
                    return retval;
                }
            }
        } else if (isDocumentSelector(selector)) {
            if (document === el as Node as Document) {
                retval = validCallback(el);
                if (undefined !== retval) {
                    return retval;
                }
            } else {
                retval = invalidCallback();
                if (undefined !== retval) {
                    return retval;
                }
            }
        } else if (isNodeSelector(selector)) {
            if (selector === el as Node) {
                retval = validCallback(el);
                if (undefined !== retval) {
                    return retval;
                }
            }
        } else if (isIterableSelector(selector)) {
            for (const elem of selector) {
                if (elem === el as Node) {
                    retval = validCallback(el);
                    if (undefined !== retval) {
                        return retval;
                    }
                }
            }
        } else {
            retval = invalidCallback();
            if (undefined !== retval) {
                return retval;
            }
        }
    }

    retval = invalidCallback();
    if (undefined !== retval) {
        return retval;
    }
}

/** @internal helper for `parent()`, `parents()` and `siblings()` */
function validParentNode(parentNode: Node | null): parentNode is Node {
    return null != parentNode && Node.DOCUMENT_NODE !== parentNode.nodeType && Node.DOCUMENT_FRAGMENT_NODE !== parentNode.nodeType;
}

/** @internal helper for `children()`, `parent()`, `next()` and `prev()` */
function validRetrieveNode<T extends SelectorBase>(node: Node | null, selector: DOMSelector<T> | undefined): node is Node {
    if (node) {
        if (selector) {
            if ($(node).is(selector)) {
                return true;
            }
        } else {
            return true;
        }
    }
    return false;
}

/** @internal helper for `nextUntil()` and `prevUntil() */
function retrieveSiblings<
    E extends ElementBase,
    T extends Node = HTMLElement,
    U extends SelectorBase = SelectorBase,
    V extends SelectorBase = SelectorBase
>(
    sibling: 'previousElementSibling' | 'nextElementSibling',
    dom: DOMTraversing<E>,
    selector?: DOMSelector<U>, filter?: DOMSelector<V>
): DOM<T> {
    if (!isTypeElement(dom)) {
        return $() as DOM<T>;
    }

    const siblings = new Set<Node>();

    for (const el of dom as DOMIterable<Element>) {
        let elem = el[sibling];
        while (elem) {
            if (null != selector) {
                if ($(elem).is(selector)) {
                    break;
                }
            }
            if (filter) {
                if ($(elem).is(filter)) {
                    siblings.add(elem);
                }
            } else {
                siblings.add(elem);
            }
            elem = elem[sibling];
        }
    }

    return $([...siblings]) as DOM<T>;
}

