/**
 * @en Mixin base class which concentrated the traversing methods.
 * @ja  Mixin Base 
 */
export class DOMTraversing<TElement extends ElementBase> implements DOMIterable<TElement> {

///////////////////////////////////////////////////////////////////////
// imprements: DOMIterable<T>

    readonly [n: number]: TElement;
    readonly length!: number;
    [Symbol.iterator]!: () => Iterator<TElement>;
    entries!: () => IterableIterator<[number, TElement]>;

///////////////////////////////////////////////////////////////////////
// public: Element Methods

    /**
     * @en Retrieve one of the elements matched by the {@link DOM} instance.
     * @ja 
     *
     * @param index
     *  - `en` A zero-based integer indicating which element to retrieve. <br>
     *         If negative index is counted from the end of the matched set.
     *  - `ja` 0 base  <br>
     *         , 
     */
    public get(index: number): TElement | undefined;

    /**
     * @en Retrieve the elements matched by the {@link DOM} instance.
     * @ja 
     */
    public get(): TElement[];

    public get(index?: number): TElement[] | TElement | undefined {
        if (null != index) {
            index = Math.trunc(index);
            return index < 0 ? this[index + this.length] : this[index];
        } else {
            return this.toArray();
        }
    }

    /**
     * @en Retrieve all the elements contained in the {@link DOM} set, as an array.
     * @ja 
     */
    public toArray(): TElement[] {
        return [...this];
    }

    /**
     * @en Return the position of the first element within the {@link DOM} collection relative to its sibling elements.
     * @ja {@link DOM} 
     */
    public index(): number | undefined;

    /**
     * @en Search for a given a selector, element, or {@link DOM} instance from among the matched elements.
     * @ja , ,  {@link DOM} , 
     */
    public index<T extends ElementBase>(selector: string | T | DOM<T>): number | undefined;

    public index<T extends ElementBase>(selector?: string | T | DOM<T>): number | undefined {
        if (!isTypeElement(this)) {
            return undefined;
        } else if (null == selector) {
            let i = 0;
            let child: Node | null = this[0];
            while (null !== (child = child.previousSibling)) {
                if (Node.ELEMENT_NODE === child.nodeType) {
                    i += 1;
                }
            }
            return i;
        } else {
            let elem: T | Element;
            if (isString(selector)) {
                elem = $(selector)[0];
            } else {
                elem = selector instanceof DOMBase ? selector[0] : selector;
            }
            const i = [...this].indexOf(elem as TElement & Element);
            return 0 <= i ? i : undefined;
        }
    }

///////////////////////////////////////////////////////////////////////
// public: Traversing

    /**
     * @en Reduce the set of matched elements to the first in the set as {@link DOM} instance.
     * @ja  {@link DOM} 
     */
    public first(): DOM<TElement> {
        return $(this[0]) as DOM<TElement>;
    }

    /**
     * @en Reduce the set of matched elements to the final one in the set as {@link DOM} instance.
     * @ja  {@link DOM} 
     */
    public last(): DOM<TElement> {
        return $(this[this.length - 1]) as DOM<TElement>;
    }

    /**
     * @en Create a new {@link DOM} instance with elements added to the set from selector.
     * @ja  `selector`  `Element`  {@link DOM} 
     *
     * @param selector
     *  - `en` Object(s) or the selector string which becomes origin of {@link DOM}.
     *  - `ja` {@link DOM} ()
     * @param context
     *  - `en` Set using `Document` context. When being un-designating, a fixed value of the environment is used.
     *  - `ja`  `Document` . .
     */
    public add<T extends SelectorBase>(selector: DOMSelector<T>, context?: QueryContext): DOM<TElement> {
        const $add = $(selector, context);
        const elems = new Set([...this, ...$add]);
        return $([...elems] as any);
    }

    /**
     * @en Check the current matched set of elements against a selector, element, or {@link DOM} instance.
     * @ja , ,  {@link DOM} , 
     *
     * @param selector
     *  - `en` Object(s) or the selector string which becomes origin of {@link DOM}, test function.
     *  - `ja` {@link DOM} (), 
     * @returns
     *  - `en` `true` if at least one of these elements matches the given arguments.
     *  - `ja`  `true` 
     */
    public is<T extends SelectorBase>(selector: DOMSelector<T> | DOMIterateCallback<TElement>): boolean {
        if (this.length <= 0 || isEmptySelector(selector as DOMSelector<T>)) {
            return false;
        }
        return winnow(selector, this, () => true, () => false) as boolean;
    }

    /**
     * @en Reduce the set of matched elements to those that match the selector or pass the function's test.
     * @ja , ,  {@link DOM} , 
     *
     * @param selector
     *  - `en` Object(s) or the selector string which becomes origin of {@link DOM}, test function.
     *  - `ja` {@link DOM} (), 
     * @returns
     *  - `en` New {@link DOM} instance including filtered elements.
     *  - `ja`   {@link DOM} 
     */
    public filter<T extends SelectorBase>(selector: DOMSelector<T> | DOMIterateCallback<TElement>): DOM<TElement> {
        if (this.length <= 0 || isEmptySelector(selector as DOMSelector<T>)) {
            return $() as DOM<TElement>;
        }
        const elements: TElement[] = [];
        winnow(selector, this, (el: TElement) => { elements.push(el); });
        return $(elements as Node[]) as DOM<TElement>;
    }

    /**
     * @en Remove elements from the set of match the selector or pass the function's test.
     * @ja , ,  {@link DOM} , 
     *
     * @param selector
     *  - `en` Object(s) or the selector string which becomes origin of {@link DOM}, test function.
     *  - `ja` {@link DOM} (), 
     * @returns
     *  - `en` New {@link DOM} instance excluding filtered elements.
     *  - `ja`   {@link DOM} 
     */
    public not<T extends SelectorBase>(selector: DOMSelector<T> | DOMIterateCallback<TElement>): DOM<TElement> {
        if (this.length <= 0 || isEmptySelector(selector as DOMSelector<T>)) {
            return $() as DOM<TElement>;
        }
        const elements = new Set<TElement>([...this]);
        winnow(selector, this, (el: TElement) => { elements.delete(el); });
        return $([...elements] as Node[]) as DOM<TElement>;
    }

    /**
     * @en Get the descendants of each element in the current set of matched elements, filtered by a selector.
     * @ja 
     *
     * @param selector
     *  - `en` Object(s) or the selector string which becomes origin of {@link DOM}.
     *  - `ja` {@link DOM} ()
     */
    public find<T extends SelectorBase = SelectorBase>(selector: DOMSelector<T>): DOMResult<T> {
        if (!isString(selector)) {
            const $selector = $(selector) as DOM<Node>;
            return $selector.filter((index, elem) => {
                for (const el of this) {
                    if (isNode(el) && el !== elem && el.contains(elem)) {
                        return true;
                    }
                }
                return false;
            }) as DOMResult<T>;
        } else if (isTypeWindow(this)) {
            return $();
        } else {
            const elements: Element[] = [];
            for (const el of this) {
                if (isNodeQueriable(el)) {
                    const elems = el.querySelectorAll(selector);
                    elements.push(...elems);
                }
            }
            return $(elements as Node[]) as DOMResult<T>;
        }
    }

    /**
     * @en Reduce the set of matched elements to those that have a descendant that matches the selector.
     * @ja 
     *
     * @param selector
     *  - `en` Object(s) or the selector string which becomes origin of {@link DOM}.
     *  - `ja` {@link DOM} ()
     */
    public has<T extends SelectorBase = SelectorBase>(selector: DOMSelector<T>): DOMResult<T> {
        if (isTypeWindow(this)) {
            return $();
        }

        const targets: Node[] = [];
        for (const el of this) {
            if (isNodeQueriable(el)) {
                const $target = $(selector, el as Element) as DOM<Element>;
                targets.push(...$target);
            }
        }

        return this.filter((index, elem) => {
            if (isNode(elem)) {
                for (const el of new Set(targets)) {
                    if (elem !== el && elem.contains(el)) {
                        return true;
                    }
                }
            }
            return false;
        }) as DOM<Node> as DOMResult<T>;
    }

    /**
     * @en Pass each element in the current matched set through a function, producing a new {@link DOM} instance containing the return values.
     * @ja  {@link DOM} 
     *
     * @param callback
     *  - `en` modification function object that will be invoked for each element in the current set.
     *  - `ja` 
     */
    public map<T extends ElementBase>(callback: DOMModificationCallback<TElement, T>): DOM<T> {
        const elements: T[] = [];
        for (const [index, el] of this.entries()) {
            elements.push(callback.call(el, index, el));
        }
        return $(elements as Node[]) as DOM<T>;
    }

    /**
     * @en Iterate over a {@link DOM} instance, executing a function for each matched element.
     * @ja 
     *
     * @param callback
     *  - `en` callback function object that will be invoked for each element in the current set.
     *  - `ja` 
     */
    public each(callback: DOMIterateCallback<TElement>): this {
        for (const [index, el] of this.entries()) {
            if (false === callback.call(el, index, el)) {
                return this;
            }
        }
        return this;
    }

    /**
     * @en Reduce the set of matched elements to a subset specified by a range of indices.
     * @ja  {@link DOM} 
     *
     * @param begin
     *  - `en` An integer indicating the 0-based position at which the elements begin to be selected.
     *  - `ja`  0 
     * @param end
     *  - `en` An integer indicating the 0-based position at which the elements stop being selected.
     *  - `ja`  0 
     */
    public slice(begin?: number, end?: number): DOM<TElement> {
        return $([...this].slice(begin, end) as Node[]) as DOM<TElement>;
    }

    /**
     * @en Reduce the set of matched elements to the one at the specified index.
     * @ja  {@link DOM} 
     *
     * @param index
     *  - `en` A zero-based integer indicating which element to retrieve. <br>
     *         If negative index is counted from the end of the matched set.
     *  - `ja` 0 base  <br>
     *         , 
     */
    public eq(index: number): DOM<TElement> {
        if (null == index) {
            // for fail safe
            return $() as DOM<TElement>;
        } else {
            return $(this.get(index)) as DOM<TElement>;
        }
    }

    /**
     * @en For each element in the set, get the first element that matches the selector by testing the element itself and traversing up through its ancestors in the DOM tree.
     * @ja . , 
     *
     * @param selector
     *  - `en` Object(s) or the selector string which becomes origin of {@link DOM}, test function.
     *  - `ja` {@link DOM} (), 
     */
    public closest<T extends SelectorBase = SelectorBase>(selector: DOMSelector<T>): DOMResult<T> {
        if (null == selector || !isTypeElement(this)) {
            return $();
        } else if (isString(selector)) {
            const closests = new Set<Node>();
            for (const el of this) {
                if (isNodeElement(el)) {
                    const c = el.closest(selector);
                    if (c) {
                        closests.add(c);
                    }
                }
            }
            return $([...closests]) as DOMResult<T>;
        } else if (this.is(selector)) {
            return $(this as unknown as Element) as DOMResult<T>;
        } else {
            return this.parents(selector).eq(0) as DOM<Node> as DOMResult<T>;
        }
    }

    /**
     * @en Get the children of each element in the set of matched elements, optionally filtered by a selector.
     * @ja . 
     *
     * @param selector
     *  - `en` filtered by a selector.
     *  - `ja` 
     */
    public children<T extends Node = HTMLElement, U extends SelectorBase = SelectorBase>(selector?: DOMSelector<U>): DOM<T> {
        if (isTypeWindow(this)) {
            return $() as DOM<T>;
        }

        const children = new Set<Node>();
        for (const el of this) {
            if (isNodeQueriable(el)) {
                for (const child of el.children) {
                    if (validRetrieveNode(child, selector)) {
                        children.add(child);
                    }
                }
            }
        }
        return $([...children]) as DOM<T>;
    }

    /**
     * @en Get the first parent of each element in the current set of matched elements.
     * @ja 
     *
     * @param selector
     *  - `en` filtered by a selector.
     *  - `ja` 
     * @returns {@link DOM} instance
     */
    public parent<T extends Node = HTMLElement, U extends SelectorBase = SelectorBase>(selector?: DOMSelector<U>): DOM<T> {
        const parents = new Set<Node>();
        for (const el of this) {
            if (isNode(el)) {
                const parentNode = el.parentNode;
                if (validParentNode(parentNode) && validRetrieveNode(parentNode, selector)) {
                    parents.add(parentNode);
                }
            }
        }
        return $([...parents]) as DOM<T>;
    }

    /**
     * @en Get the ancestors of each element in the current set of matched elements.
     * @ja 
     *
     * @param selector
     *  - `en` filtered by a selector.
     *  - `ja` 
     * @returns {@link DOM} instance
     */
    public parents<T extends Node = HTMLElement, U extends SelectorBase = SelectorBase>(selector?: DOMSelector<U>): DOM<T> {
        return this.parentsUntil(undefined, selector);
    }

    /**
     * @en Get the ancestors of each element in the current set of matched elements, <br>
     *     up to but not including the element matched by the selector, DOM node, or {@link DOM} instance
     * @ja , 
     *
     * @param selector
     *  - `en` Object(s) or the selector string which becomes origin of {@link DOM}.
     *  - `ja` {@link DOM} ()
     * @param filter
     *  - `en` filtered by a string selector.
     *  - `ja` 
     * @returns {@link DOM} instance
     */
    public parentsUntil<
        T extends Node = HTMLElement,
        U extends SelectorBase = SelectorBase,
        V extends SelectorBase = SelectorBase
    >(selector?: DOMSelector<U>, filter?: DOMSelector<V>): DOM<T> {
        let parents: Node[] = [];

        for (const el of this) {
            let parentNode = (el as Node).parentNode;
            while (validParentNode(parentNode)) {
                if (null != selector) {
                    if ($(parentNode).is(selector)) {
                        break;
                    }
                }
                if (filter) {
                    if ($(parentNode).is(filter)) {
                        parents.push(parentNode);
                    }
                } else {
                    parents.push(parentNode);
                }
                parentNode = parentNode.parentNode;
            }
        }

        // 
        if (1 < this.length) {
            parents = [...new Set(parents.reverse())].reverse();
        }

        return $(parents) as DOM<T>;
    }

    /**
     * @en Get the immediately following sibling of each element in the set of matched elements. <br>
     *     If a selector is provided, it retrieves the next sibling only if it matches that selector.
     * @ja  <br>
     *     
     *
     * @param selector
     *  - `en` filtered by a selector.
     *  - `ja` 
     */
    public next<T extends Node = HTMLElement, U extends SelectorBase = SelectorBase>(selector?: DOMSelector<U>): DOM<T> {
        if (!isTypeElement(this)) {
            return $() as DOM<T>;
        }

        const nextSiblings = new Set<Node>();
        for (const el of this) {
            if (isNodeElement(el)) {
                const elem = el.nextElementSibling;
                if (validRetrieveNode(elem, selector)) {
                    nextSiblings.add(elem);
                }
            }
        }
        return $([...nextSiblings]) as DOM<T>;
    }

    /**
     * @en Get all following siblings of each element in the set of matched elements, optionally filtered by a selector.
     * @ja . .
     *
     * @param selector
     *  - `en` filtered by a selector.
     *  - `ja` 
     */
    public nextAll<T extends Node = HTMLElement, U extends SelectorBase = SelectorBase>(selector?: DOMSelector<U>): DOM<T> {
        return this.nextUntil(undefined, selector);
    }

    /**
     * @en Get all following siblings of each element up to but not including the element matched by the selector.
     * @ja , 
     *
     * @param selector
     *  - `en` Object(s) or the selector string which becomes origin of {@link DOM}.
     *  - `ja` {@link DOM} ()
     * @param filter
     *  - `en` filtered by a string selector.
     *  - `ja` 
     */
    public nextUntil<
        T extends Node = HTMLElement,
        U extends SelectorBase = SelectorBase,
        V extends SelectorBase = SelectorBase
    >(selector?: DOMSelector<U>, filter?: DOMSelector<V>): DOM<T> {
        return retrieveSiblings('nextElementSibling', this, selector, filter);
    }

    /**
     * @en Get the immediately preceding sibling of each element in the set of matched elements. <br>
     *     If a selector is provided, it retrieves the previous sibling only if it matches that selector.
     * @ja  <br>
     *     
     *
     * @param selector
     *  - `en` filtered by a selector.
     *  - `ja` 
     */
    public prev<T extends Node = HTMLElement, U extends SelectorBase = SelectorBase>(selector?: DOMSelector<U>): DOM<T> {
        if (!isTypeElement(this)) {
            return $() as DOM<T>;
        }

        const prevSiblings = new Set<Node>();
        for (const el of this) {
            if (isNodeElement(el)) {
                const elem = el.previousElementSibling;
                if (validRetrieveNode(elem, selector)) {
                    prevSiblings.add(elem);
                }
            }
        }
        return $([...prevSiblings]) as DOM<T>;
    }

    /**
     * @en Get all preceding siblings of each element in the set of matched elements, optionally filtered by a selector.
     * @ja . .
     *
     * @param selector
     *  - `en` filtered by a selector.
     *  - `ja` 
     */
    public prevAll<T extends Node = HTMLElement, U extends SelectorBase = SelectorBase>(selector?: DOMSelector<U>): DOM<T> {
        return this.prevUntil(undefined, selector);
    }

    /**
     * @en Get all preceding siblings of each element up to but not including the element matched by the selector.
     * @ja , 
     *
     * @param selector
     *  - `en` Object(s) or the selector string which becomes origin of {@link DOM}.
     *  - `ja` {@link DOM} ()
     * @param filter
     *  - `en` filtered by a string selector.
     *  - `ja` 
     */
    public prevUntil<
        T extends Node = HTMLElement,
        U extends SelectorBase = SelectorBase,
        V extends SelectorBase = SelectorBase
    >(selector?: DOMSelector<U>, filter?: DOMSelector<V>): DOM<T> {
        return retrieveSiblings('previousElementSibling', this, selector, filter);
    }

    /**
     * @en Get the siblings of each element in the set of matched elements, optionally filtered by a selector
     * @ja 
     *
     * @param selector
     *  - `en` filtered by a selector.
     *  - `ja` 
     */
    public siblings<T extends Node = HTMLElement, U extends SelectorBase = SelectorBase>(selector?: DOMSelector<U>): DOM<T> {
        if (!isTypeElement(this)) {
            return $() as DOM<T>;
        }

        const siblings = new Set<Node>();
        for (const el of this) {
            if (isNodeElement(el)) {
                const parentNode = el.parentNode;
                if (validParentNode(parentNode)) {
                    for (const sibling of $(parentNode).children(selector)) {
                        if (sibling !== el as Element) {
                            siblings.add(sibling);
                        }
                    }
                }
            }
        }
        return $([...siblings]) as DOM<T>;
    }

    /**
     * @en Get the children of each element in the set of matched elements, including text and comment nodes.
     * @ja HTML
     */
    public contents<T extends Node = HTMLElement>(): DOM<T> {
        if (isTypeWindow(this)) {
            return $() as DOM<T>;
        }

        const contents = new Set<Node>();
        for (const el of this) {
            if (isNode(el)) {
                if (nodeName(el, 'iframe')) {
                    contents.add((el as HTMLIFrameElement).contentDocument as Node);
                } else if (nodeName(el, 'template')) {
                    contents.add((el as HTMLTemplateElement).content);
                } else {
                    for (const node of el.childNodes) {
                        contents.add(node);
                    }
                }
            }
        }
        return $([...contents]) as DOM<T>;
    }

    /**
     * @en Get the closest ancestor element that is positioned.
     * @ja , (positiionrelative, absolute, fixed)
     */
    public offsetParent<T extends Node = HTMLElement>(): DOM<T> {
        const rootElement = document.documentElement;
        if (this.length <= 0) {
            return $() as DOM<T>;
        } else if (!isTypeElement(this)) {
            return $(rootElement) as DOM<Node> as DOM<T>;
        } else {
            const offsets = new Set<Node>();
            for (const el of this) {
                const offset = getOffsetParent(el as Node) ?? rootElement;
                offsets.add(offset);
            }
            return $([...offsets]) as DOM<T>;
        }
    }
}

setMixClassAttribute(DOMTraversing, 'protoExtendsOnly');
