/**
 * @en Cursor position constant.
 * @ja 
 */
export const enum CursorPos {
    OUT_OF_RANGE    = -1,
    CURRENT         = -2,
}

/**
 * @en Seek expression function type.
 * @ja 
 */
export type SeekExp<T> = (value: T, index?: number, obj?: T[]) => boolean;

/**
 * @en The class provides cursor interface for Array. <br>
 *     It is different from Iterator interface of es2015, and that provides interface which is similar to DB recordset's one.
 * @ja Array  I/F  <br>
 *     es2015  Iterator I/F DB recordset  I/F 
 */
export class ArrayCursor<T = any> { // eslint-disable-line @typescript-eslint/no-explicit-any
    /** @internal   */
    private _array: T[];
    /** @internal  true  */
    private _bof: boolean;
    /** @internal  true */
    private _eof: boolean;
    /** @internal  index */
    private _index: number;

    /**
     * constructor
     *
     * @param array
     *  - `en` target array
     *  - `ja` 
     * @param initialIndex
     *  - `en` initial index. default: 0
     *  - `ja`  index  default: 0
     */
    constructor(array: T[], initialIndex = 0) {
        this._array = array;
        this._index = initialIndex;
        if (this.valid()) {
            this._bof = this._eof = false;
        } else {
            this._index = CursorPos.OUT_OF_RANGE;
            this._bof = true;
            this._eof = false;
        }
    }

    /**
     * @en Reset target array.
     * @ja 
     *
     * @param array
     *  - `en` target array. default: empty array.
     *  - `ja` .   default: 
     * @param initialIndex
     *  - `en` initial index. default: CURSOR.OUT_OF_RANGE
     *  - `ja`  index  default: CURSOR.OUT_OF_RANGE
     */
    public reset(array: T[] = [], initialIndex: number = CursorPos.OUT_OF_RANGE): ArrayCursor<T> {
        this._array = array;
        this._index = initialIndex;
        if (this.valid()) {
            this._bof = this._eof = false;
        } else {
            this._index = CursorPos.OUT_OF_RANGE;
            this._bof = true;
            this._eof = false;
        }
        return this;
    }

///////////////////////////////////////////////////////////////////////
// accessors:

    /**
     * @en Access to current element.
     * @ja 
     */
    get current(): T | undefined {
        return this._array[this._index];
    }

    /**
     * @en Get current index.
     * @ja  index 
     */
    get index(): number {
        return this._index;
    }

    /**
     * @en Get target array length.
     * @ja 
     */
    get length(): number {
        return this._array.length;
    }

    /**
     * @en Judge BOF or not.
     * @ja 
     */
    get isBOF(): boolean {
        return this._bof;
    }

    /**
     * @en Judge EOF or not.
     * @ja 
     */
    get isEOF(): boolean {
        return this._eof;
    }

    /**
     * @en Access to raw array instance.
     * @ja 
     */
    get array(): T[] {
        return this._array;
    }

///////////////////////////////////////////////////////////////////////
// cursor operation:

    /**
     * @en Move to first element position.
     * @ja 
     */
    public moveFirst(): ArrayCursor<T> {
        this._index = 0;
        this._bof = this._eof = false;
        if (!this.valid()) {
            this._index = CursorPos.OUT_OF_RANGE;
            this._bof = true;
        }
        return this;
    }

    /**
     * @en Move to last element position.
     * @ja 
     */
    public moveLast(): ArrayCursor<T> {
        this._index = this._array.length - 1;
        this._bof = this._eof = false;
        if (!this.valid()) {
            this._eof = true;
        }
        return this;
    }

    /**
     * @en Move to next element position.
     * @ja 
     */
    public moveNext(): ArrayCursor<T> {
        if (this._bof) {
            this._bof = false;
            this._index = 0;
        } else {
            this._index++;
        }
        if (!this.valid()) {
            this._index = CursorPos.OUT_OF_RANGE;
            this._eof = true;
        }
        return this;
    }

    /**
     * @en Move to previous element position.
     * @ja 
     */
    public movePrevious(): ArrayCursor<T> {
        if (this._eof) {
            this._eof = false;
            this._index = this.length - 1;
        } else {
            this._index--;
        }
        if (!this.valid()) {
            this._index = CursorPos.OUT_OF_RANGE;
            this._bof = true;
        }
        return this;
    }

    /**
     * @en Seek by passed criteria. <br>
     *     If the operation failed, the cursor position set to EOF.
     * @ja  <br>
     *      EOF 
     *
     * @param criteria
     *  - `en` index or seek expression
     *  - `ja` index / 
     */
    public seek(criteria: number | SeekExp<T>): ArrayCursor<T> {
        if ('number' === typeof criteria) {
            this._index = criteria;
        } else {
            this._index = this._array.findIndex(criteria);
        }
        if (!this.valid()) {
            this._index = CursorPos.OUT_OF_RANGE;
            this._bof = false;
            this._eof = true;
        }
        return this;
    }

///////////////////////////////////////////////////////////////////////
// private methods:

    /**
     * 
     *
     * @internal
     *
     * @returns true:  / false: 
     */
    private valid(): boolean {
        return (0 <= this._index && this._index < this._array.length);
    }
}
