import { IDataSync } from './interfaces';
import { dataSyncNULL } from './null';

/** @internal */ let _default: IDataSync = dataSyncNULL;

/**
 * @en Get or update default {@link IDataSync} object.
 * @ja  {@link IDataSync}  / 
 *
 * @param newSync
 *  - `en` new data-sync object. if `undefined` passed, only returns the current object.
 *  - `ja`  data-sync . `undefined`  data-sync 
 * @returns
 *  - `en` old data-sync object.
 *  - `ja`  data-sync 
 */
export function defaultSync(newSync?: IDataSync): IDataSync {
    if (null == newSync) {
        return _default;
    } else {
        const oldSync = _default;
        _default = newSync;
        return oldSync;
    }
}
