/* eslint-disable
    @typescript-eslint/no-explicit-any,
 */

import {
    post,
    deepEqual,
    deepCopy,
    dropUndefined,
} from '@cdp/core-utils';
import { EventPublisher } from '@cdp/events';
import {
    StorageData,
    IStorage,
    IStorageOptions,
    IStorageFormatOptions,
    RegistrySchemaBase,
    RegistryEvent,
    RegistryReadOptions,
    RegistryWriteOptions,
    RegistrySaveOptions,
} from './interfaces';

/**
 * @en Registry management class for synchronous Read/Write accessible from any {@link IStorage} object.
 * @ja  {@link IStorage}  Read/Write 
 *
 * @example <br>
 *
 * ```ts
 * // 1. define registry schema
 * interface Schema extends RegistrySchemaBase {
 *    'common/mode': 'normal' | 'specified';
 *    'common/value': number;
 *    'trade/local': { unit: '' | '$'; rate: number; };
 *    'trade/check': boolean;
 *    'extra/user': string;
 * }
 *
 * // 2. prepare IStorage instance
 * // ex
 * import { webStorage } from '@cdp/runtime';
 *
 * // 3. instantiate this class
 * const reg = new Registry<Schema>(webStorage, '@test');
 *
 * // 4. read example
 * const val = reg.read('common/mode'); // 'normal' | 'specified' | null
 *
 * // 5. write example
 * reg.write('common/mode', 'specified');
 * // reg.write('common/mode', 'hoge'); // compile error
 * ```
 */
export class Registry<T extends RegistrySchemaBase = any> extends EventPublisher<RegistryEvent<T>> {

    /** @internal */
    private readonly _storage: IStorage;
    /** @internal */
    private readonly _rootKey: string;
    /** @internal */
    private readonly _defaultOptions: IStorageFormatOptions;
    /** @internal */
    private _store: StorageData = {};

    /**
     * constructor
     *
     * @param storage
     *  - `en` Root key for {@link IStorage}.
     *  - `ja` {@link IStorage} 
     * @param rootKey
     *  - `en` Root key for {@link IStorage}.
     *  - `ja` {@link IStorage} 
     * @param formatSpace
     *  - `en` for JSON format space.
     *  - `ja` JSON 
     */
    constructor(storage: IStorage<any>, rootKey: string, formatSpace?: number) {
        super();
        this._storage = storage;
        this._rootKey = rootKey;
        this._defaultOptions = { jsonSpace: formatSpace };
    }

    /**
     * @en Access to root key.
     * @ja 
     */
    get rootKey(): string {
        return this._rootKey;
    }

    /**
     * @en Access to {@link IStorage} object.
     * @ja {@link IStorage} 
     */
    get storage(): IStorage<any> {
        return this._storage;
    }

///////////////////////////////////////////////////////////////////////
// public methods:

    /**
     * @en Read persistence data from {@link IStorage}. The data loaded already will be cleared.
     * @ja {@link IStorage} . 
     */
    public async load(options?: IStorageOptions): Promise<void> {
        options = options ?? {};
        this._store = (await this._storage.getItem(this._rootKey, options)) || {};
        if (!options.silent) {
            void post(() => this.publish('change', '*'));
        }
    }

    /**
     * @en Persist data to {@link IStorage}.
     * @ja {@link IStorage} 
     */
    public async save(options?: RegistrySaveOptions): Promise<void> {
        const opts: RegistrySaveOptions = { ...this._defaultOptions, ...options };
        if (!opts.silent) {
            this.publish('will-save');
        }
        await this._storage.setItem(this._rootKey, this._store, opts);
    }

    /**
     * @en Read registry value.
     * @ja 
     *
     * @param key
     *  - `en` target registry key.
     *  - `ja` 
     * @param options
     *  - `en` read options.
     *  - `ja` 
     */
    public read<K extends keyof T>(key: K, options?: RegistryReadOptions): T[K] | null {
        const { field } = options ?? {};
        const structure = String(key).split('/');
        const lastKey = structure.pop()!;

        let name: string | undefined;
        let reg = this.targetRoot(field);

        while (name = structure.shift()) { // eslint-disable-line no-cond-assign
            if (!(name in reg)) {
                return null;
            }
            reg = reg[name] as StorageData;
        }

        // return deep copy
        return (null != reg[lastKey]) ? deepCopy(reg[lastKey]) as any : null;
    }

    /**
     * @en Write registry value.
     * @ja 
     *
     * @param key
     *  - `en` target registry key.
     *  - `ja` 
     * @param value
     *  - `en` update value. if `null` set to delete.
     *  - `ja` . `null` 
     * @param options
     *  - `en` write options.
     *  - `ja` 
     */
    public write<K extends keyof T>(key: K, value: T[K] | null, options?: RegistryWriteOptions): void {
        const { field, noSave, silent } = options ?? {};
        const remove = (null == value);
        const structure = String(key).split('/');
        const lastKey = structure.pop()!;

        let name: string | undefined;
        let reg = this.targetRoot(field);

        while (name = structure.shift()) { // eslint-disable-line no-cond-assign
            if (name in reg) {
                reg = reg[name] as StorageData;
            } else if (remove) {
                return; // 
            } else {
                reg = reg[name] = {};
            }
        }

        const newVal = remove ? null : value;
        const oldVal = dropUndefined(reg[lastKey]);
        if (deepEqual(oldVal, newVal)) {
            return; // 
        } else if (remove) {
            delete reg[lastKey];
        } else {
            reg[lastKey] = deepCopy(newVal) as any;
        }

        if (!noSave) {
            // no fire notification
            void this._storage.setItem(this._rootKey, this._store, { ...this._defaultOptions, ...options });
        }

        if (!silent) {
            void post(() => this.publish('change', key, newVal, oldVal as any));
        }
    }

    /**
     * @en Delete registry key.
     * @ja 
     *
     * @param key
     *  - `en` target registry key.
     *  - `ja` 
     * @param options
     *  - `en` read options.
     *  - `ja` 
     */
    public delete<K extends keyof T>(key: K, options?: RegistryWriteOptions): void {
        this.write(key, null, options);
    }

    /**
     * @en Clear all registry.
     * @ja 
     *
     * @param options
     *  - `en` read options.
     *  - `ja` 
     */
    public clear(options?: RegistryWriteOptions): void {
        options = options ?? {};
        this._store = {};
        void this._storage.removeItem(this._rootKey, options);
        if (!options.silent) {
            this.publish('change', null, null, null);
        }
    }

///////////////////////////////////////////////////////////////////////
// private methods:

    /** @internal get root object */
    private targetRoot(field?: string): StorageData {
        if (field) {
            // ensure [field] object.
            this._store[field] = this._store[field] || {};
            return this._store[field] as StorageData;
        } else {
            return this._store;
        }
    }
}
