import type { UnknownObject } from '@cdp/core-utils';
import { Subscription, EventBroker } from '@cdp/events';
import { _internal } from './internal';

/**
 * @en Event observation state definition.
 * @ja 
 */
export const enum ObservableState {
    /** observable ready */
    ACTIVE   = 'active',
    /** NOT observed, but property changes are recorded. */
    SUSEPNDED = 'suspended',
    /** NOT observed, and not recording property changes. */
    DISABLED = 'disabled',
}

/**
 * @en Observable common interface.
 * @ja Observable 
 */
export interface IObservable {
    /**
     * @en Subscrive event(s).
     * @ja 
     */
    on(...args: unknown[]): Subscription;

    /**
     * @en Unsubscribe event(s).
     * @ja 
     */
    off(...args: unknown[]): void;

    /**
     * @en Suspend or disable the event observation state.
     * @ja 
     *
     * @param noRecord
     *  - `en` `true`: not recording property changes and clear changes. / `false`: property changes are recorded and fired when {@link resume}() callded. (default)
     *  - `ja` `true`: ,  / `false`: , {@link resume}()  ()
     */
    suspend(noRecord?: boolean): this;

    /**
     * @en Resume the event observation state.
     * @ja 
     */
    resume(): this;

    /**
     * @en observation state
     * @ja 
     */
    getObservableState(): ObservableState;
}

/**
 * @en Interface able to access to {@link EventBroker} with {@link IObservable}.
 * @ja {@link IObservable}  {@link EventBroker} 
 */
export interface IObservableEventBrokerAccess<T extends object = any> extends IObservable { // eslint-disable-line @typescript-eslint/no-explicit-any
    /**
     * @en Get {@link EventBroker} instance.
     * @ja {@link EventBroker} 
     */
    getBroker(): EventBroker<T>;
}

/**
 * @en Check the value-type is {@link IObservable}.
 * @ja {@link IObservable} 
 *
 * @param x
 *  - `en` evaluated value
 *  - `ja` 
 */
export function isObservable(x: unknown): x is IObservable {
    return Boolean(x && (x as UnknownObject)[_internal]);
}
